:root {
  --primary: #2563eb;
  --primary-light: #3b82f6;
  --bg: #f8fafc;
  --text: #1e293b;
  --border: #e2e8f0;
}

body {
  background: var(--bg);
  color: var(--text);
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, sans-serif;
}

.demo-nav {
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  gap: 12px;
  padding: 16px;
  display: flex;
  box-shadow: 0 1px 3px #0000001a;
}

.section-nav, .method-nav {
  flex-wrap: wrap;
  gap: 8px;
  display: flex;
}

.demo-nav a {
  color: var(--text);
  border: 1px solid var(--border);
  background: #fff;
  border-radius: 6px;
  padding: 8px 16px;
  font-family: SF Mono, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  transition: all .2s;
}

.demo-nav a:hover {
  background: var(--primary-light);
  border-color: var(--primary-light);
  color: #fff;
}

.demo-nav a.active {
  background: var(--primary);
  border-color: var(--primary);
  color: #fff;
}

canvas {
  cursor: crosshair;
  background: #fff;
  border-radius: 8px;
  margin: 20px 0;
  box-shadow: 0 1px 3px #0000001a;
}

.method-nav {
  background: #f1f5f9;
  border-radius: 6px;
  padding: 8px;
}

.method-nav a {
  background: #fff;
  font-size: 13px;
}
